.block--j .icons-content-block-vertical {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  margin: 0 0 -25px;
}
@media screen and (min-width: 1025px) {
  .block--j .icons-content-block-vertical {
    margin: -1.563vw -0.625vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--j .icons-content-block-vertical {
    margin: -30px -12px;
  }
}
.block--j .icons-content-block-vertical .txt-post-rtf {
  width: 100%;
}
.block--j .icons-content-block-vertical ul {
  margin-left: 0px !important;
}
.block--j .icons-content-block-vertical .icons-block-item {
  width: 100%;
  margin: 0 0 25px;
}
@media screen and (min-width: 1025px) {
  .block--j .icons-content-block-vertical .icons-block-item {
    width: calc(33.33% - 1.25vw);
    margin: 1.563vw 0.625vw;
    padding-right: 7.813vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--j .icons-content-block-vertical .icons-block-item {
    width: calc(33.33% - 24px);
    margin: 30px 12px;
    padding-right: 150px;
  }
}
.block--j .icons-content-block-vertical .img-wrap {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 25px;
}
@media screen and (max-width: 550px) {
  .block--j .icons-content-block-vertical .img-wrap {
    height: 18vw;
    justify-content: flex-start;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .block--j .icons-content-block-vertical .img-wrap {
    height: 90px;
    justify-content: flex-start;
  }
}
@media screen and (min-width: 1025px) {
  .block--j .icons-content-block-vertical .img-wrap {
    height: 4.688vw;
    margin-bottom: 1.302vw;
    padding-right: 2.083vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--j .icons-content-block-vertical .img-wrap {
    height: 90px;
    margin-bottom: 25px;
    padding-right: 40px;
  }
}
.block--j .icons-content-block-vertical .img-wrap img {
  max-height: 100%;
}
.block--j .img--wrap,
.block--j .img--wrap img {
  width: 90px;
  height: 75px;
  margin-bottom: 25px;
}
.block--j .img--wrap img {
  object-fit: contain;
  object-position: left center;
  margin-bottom: 15px;
}
.block--j .img--container {
  text-align: center;
}
.block--j .img--container img {
  max-width: 300px;
  max-height: 300px;
}
@media screen and (max-width: 767px) {
  .block--j .img--container img {
    width: 100%;
    height: 100%;
  }
}
.block--j p.h6 {
  font-size: 1.094rem !important;
  letter-spacing: 0.026vw !important;
  color: #002ea6 !important;
  font-weight: 700 !important;
}
@media screen and (max-width: 1024px) {
  .block--j .icons--item {
    margin-bottom: 64px;
  }
}
.block--j .icons--item .img--container {
  margin-bottom: 32px;
}
.block--j .icons--item:last-of-type {
  margin-bottom: 0;
}